import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import Button from '@material-ui/core/Button';
import { fade } from '@material-ui/core/styles/colorManipulator';
import { withStyles } from '@material-ui/core/styles';
import SearchIcon from '@material-ui/icons/Search';
import MenuIcon from '@material-ui/icons/Menu';



const styles = theme => ({
  root: {
    width: '100%',

  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    fontFamily: `'Josefin Sans', sans-serif !important`,
    textTransform: 'none !important',
    fontSize: '1.4rem'
  },
  title: {
    display: 'block',
    cursor: 'pointer',
    font: `35px 'Dancing Script', cursive !important`,
    paddingLeft: '5%' 
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.25),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.45),
    },
    marginRight: theme.spacing.unit * 2,
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing.unit * 3,
      width: 'auto',
    },
  },
  searchIcon: {
    width: theme.spacing.unit * 9,
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
    width: '100%',
  },
  inputInput: {
    paddingTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    paddingLeft: theme.spacing.unit * 10,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: 100,
    },
  },
  sectionDesktop: {
    display: 'none',
    paddingRight: '5%',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    paddingRight: '5%',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
});

class Navigation extends React.Component {
  state = {
    anchorEl: null,
    mobileMoreAnchorEl: null,
  };

  handleProfileMenuOpen = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleMenuClose = () => {
    this.setState({ anchorEl: null });
    this.handleMobileMenuClose();
  };

  handleMobileMenuOpen = event => {
    this.setState({ mobileMoreAnchorEl: event.currentTarget });
  };

  handleMobileMenuClose = () => {
    this.setState({ mobileMoreAnchorEl: null });
  };

  render() {
    const { anchorEl, mobileMoreAnchorEl } = this.state;
    const { classes, onRouteChange } = this.props;
    const isMenuOpen = Boolean(anchorEl);
    const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

    const renderMenu = (
      <Menu
        anchorEl={anchorEl}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        transformOrigin={{ vertical: 'top', horizontal: 'right' }}
        open={isMenuOpen}
        onClose={this.handleMenuClose}
      >
        <MenuItem onClick={this.handleMenuClose}>Povestea mea</MenuItem>
        <MenuItem onClick={this.handleMenuClose}>Blog</MenuItem>
      </Menu>
    );

    const renderMobileMenu = (
      <Menu
        anchorEl={mobileMoreAnchorEl}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        transformOrigin={{ vertical: 'top', horizontal: 'right' }}
        open={isMobileMenuOpen}
        onClose={this.handleMenuClose}
      >
        <MenuItem onClick={this.handleMobileMenuClose}>
          <p className={classes.menuButton}>Lifestyle</p>
        </MenuItem>
        <MenuItem onClick={this.handleMobileMenuClose}>
          <p className={classes.menuButton} onClick={() => onRouteChange('workshop')}>Workshops</p>
        </MenuItem>
        <MenuItem onClick={this.handleMobileMenuClose}>
          <p className={classes.menuButton} onClick={() => onRouteChange('vlog')}>Vlog</p>
        </MenuItem>
        <MenuItem onClick={this.handleMobileMenuClose}>
          <p className={classes.menuButton}>Contact</p>
        </MenuItem>
      </Menu>
    );

    return (
      <div className={classes.root}>

        <AppBar position="fixed" style={{background: 'linear-gradient(0deg, hsla(0, 0%, 100%, 0), #c71585)', boxShadow: 'none'}}>
          <Toolbar>
            <Typography 
              onClick={() => onRouteChange('home')}
              className={classes.title} 
              variant="h6" 
              color="inherit" 
              noWrap
              >
              Diana Parvu
            </Typography>

            <div className={classes.grow} />
            <div className={classes.sectionDesktop}>
              <Button className={classes.menuButton} color="inherit">
                Lifestyle
              </Button>
              <Button className={classes.menuButton} onClick={() => onRouteChange('workshop')} color="inherit">
                Workshops
              </Button>
              <Button className={classes.menuButton} onClick={() => onRouteChange('vlog')} color="inherit">
                Vlog
              </Button>
              <Button className={classes.menuButton} color="inherit">
                Contact
              </Button>
            </div>
            <div className={classes.sectionMobile}>
              <IconButton aria-haspopup="true" onClick={this.handleMobileMenuOpen} color="inherit">
                <MenuIcon />
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
        {renderMenu}
        {renderMobileMenu}
      </div>
    );
  }
}

Navigation.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Navigation);